/**
 * Rest custom angular service
 */

(function() {
  'use strict';

  angular.module('app.rest')
    .service('RestService', RestService);

  /** @ngInject */
  function RestService($http, $q, $location) {
    var rest = this;

    rest.url = $location.protocol() + '://' + $location.host() + ':' + $location.port() + '/api/v1/';
    // rest.url = 'http://127.0.0.1:8080/api/v1/';

    /**
     * Function returing the end point url
     * @method: endPoint
     */
    rest.endPoint = function() {
      return rest.url || 'http://localhost:3000';
    }

    /**
     * Function to make the rest call only for GET method
     * @method: get
     */
    rest.get = function(url, data) {
      return $q(function(resolve, reject) {
        $http({
            method: 'GET',
            url: rest.url + url,
            data: (data) ? data : ''
          })
          .then(function(response) {
            if (response && response.data.success) {
              resolve(response);
            } else {
              reject(response);
            }
          }, function(err) {
            reject(err);
          });
      });
    }

    /**
     * Function to make the rest call only for POST method
     * @method: post
     */
    rest.post = function(url, data) {
      return $q(function(resolve, reject) {
        $http({
            method: 'POST',
            url: rest.url + url,
            data: data
          })
          .then(function(response) {
            if (response && response.data.success) {
              resolve(response);
            } else {
              reject(response);
            }
          }, function(err) {
            reject(err);
          });
      });
    }

    /**
     * Function to make a rest call for only put method
     * @method: put
     */
    rest.put = function(url, data) {
      return $q(function(resolve, reject) {
        $http({
            method: 'PUT',
            url: rest.url + url,
            data: data
          })
          .then(function(response) {
            if (response && response.data.success) {
              resolve(response);
            } else {
              reject(response);
            }
          }, function(err) {
            reject(err);
          });
      });
    }

    /**
     * Function to make a rest call for only delete mainly for logout
     * @method: delete
     */
    rest.delete = function(url, data) {
      return $q(function(resolve, reject) {
        // intercepting http service request.
        // $httpProvider.interceptors.push('AuthInterceptor');

        $http({
            method: 'DELETE',
            url: rest.url + url,
            data: data
          })
          .then(function(response) {
            if (response && response.data.success) {
              resolve(response);
            } else {
              reject(response);
            }
          }, function(err) {
            reject(err);
          })
      });
    }

    return rest;
  }
}());

/**
 * registration controller
 */

(function() {
  'use strict';

  angular.module('app.register.controller', ['ui.bootstrap'])
    .controller('RegisterController', RegisterController);

  /** @ngInject */
  function RegisterController(RestService, AuthService, $log, $location) {

    var vm = this;
    vm.isError = false;
    /**
     * Check if the passwords matched, and register the user.
     * @method: registerUser
     */
    vm.registerUser = function() {
      if (vm.password === vm.confirmPassword) {
        var payload = {
          emailAddress: vm.emailAddress,
          password: vm.password,
          firstName: vm.firstName,
          lastName: vm.lastName,
          contact: vm.contact,
          gender: vm.gender
        };

        RestService.post('register', payload)
          .then(function(response) {
            if (response.data) {
              $location.path('/');
            } else {
              $log.error(response.data.message);
              vm.isError = true;
              vm.errorMessage = response.data.message;
            }
          }, function(err) {
            $log.error(err);
            vm.isError = true;
            vm.errorMessage = 'Some error while registering the user.';
          });
      } else {
        $log.error('passwords do not match');
        vm.isError = true;
        vm.errorMessage = 'Passwords do not match';
      }
    };
  }
}());

(function() {
  'use strict';

  angular.module('app.login.controller', [])
    .controller('LoginController', LoginController);

  /** @ngInject */
  function LoginController(RestService, AuthService, $location, $log) {
    var vm = this;

    vm.isError = false;

    vm.login = function() {
      if (vm.email && vm.password) {
        RestService.post('login', {
            email: vm.email,
            password: vm.password
          })
          .then(function(response) {
            if(response.data) {
              AuthService.logIn(response.data.token, response.data.user.role, angular.toJson(response.data.user));
              $location.path('/');
            } else {
              vm.isError = true;
              vm.errorMessage = 'Something went wrong';
            }
          }, function(err) {
            vm.isError = true;
            vm.errorMessage = 'Error while logging in.';
            $log.error(err);
          });
      }
    }
  }
}());

(function() {
  'use strict';

  angular
    .module('app.landing.controller', [])
    .controller('LandingController', LandingController);

  /** @ngInject */
  function LandingController($log, RestService) {
    var vm = this;

    // Data
    vm.isPopularCoursesAvailable = false;
    vm.isUpcomingActivitiesAvailable = false;

    // methods

    /**
     * GET method call to retrieve the popular courses
     */
    RestService.get('courses/popular')
      .then(function(response) {
        if (response.data && response.data.courses.length > 0) {
          vm.isPopularCoursesAvailable = true;
          vm.popularCourses = response.data.courses;
        } else {
          $log.error('No popular courses found');
        }
      }, function(err) {
        $log.error(err);
      });


    /**
     * GET method call to retrieve the upcoming activities.
     */
    RestService.get('activities/upcoming')
      .then(function(response) {
        if (response.data && response.data.activities.length > 0) {
          vm.isUpcomingActivitiesAvailable = true;
          vm.response.data.activityList = response.data.activities;
        } else {
          $log.error('No upcoming activities found');
        }
      }, function(err) {
        $log.error(err);
      });
  }
})();

/**
 * Courses controller function, rendering the list of courses for the user and navigating the user on select of course
 */

(function() {
  'use strict';

  angular.module('app.courses.controller', [])
    .controller('CoursesController', CoursesController);

  /** @ngInject */
  function CoursesController(RestService, $log) {
    var vm = this;

    //Data
    vm.page = 1;
    vm.showMoreAvaiable = false;
    var iconsArray = {
      MUSIC: 'icon-music-3',
      DANCE: 'icon-pitch',
      SOCCER: 'icon-soccer',
      SPORTS: 'icon-skiing',
      EDUCATION: 'icon-library',
      CRICKET: 'icon-cricket'
    };
    vm.priceList = [{
      id: 'lowest,price',
      name: 'Lowest price'
    }, {
      id: 'highest,price',
      name: 'Highest price'
    }];
    vm.ratingsList = [{
      id: 'lowest,ratings',
      name: 'Lowest ranking'
    }, {
      id: 'highest,ratings',
      name: 'Highest ranking'
    }];


    /**
     * Rest service call to get the list of category for courses
     */
    RestService.get('categories')
      .then(function(response) {
        if (response.data) {
          var categories = [];
          for (var i = 0; i < response.data.categoryList.length; i++) {
            var obj = {};
            obj.categoryID = response.data.categoryList[i].categoryID;
            obj.iconClass = iconsArray[response.data.categoryList[i].categoryName.toUpperCase()];
            obj.categoryName = response.data.categoryList[i].categoryName;
            categories.push(obj);
          }
          vm.categories = categories;
        }
      }, function(err) {
        $log.error(err);
      });


    /**
     * Function to handle show more to load more activities.
     * @method: showMore
     */
    vm.showMore = function() {
      getCourses(vm.pageNo + 1);
    }

    /**
     * Function to handle sort to load sorted courses
     * @method: applySort
     */

    vm.applySort = function(type, order) {
      vm.pageNo = 1;
      var sort = {};
      sort.type = type;
      sort.order = order;
      getCourses(vm.pageNo, sort);
    };


    /**
     * Function to handle filter to load filtered courses
     * @method: applyFilter
     */
    vm.applyFilter = function(type, value) {
      vm.pageNo = 1;
      var filter = {};
      filter.type = type;
      if (value) {
        filter.value = value;
      }
      getCourses(vm.pageNo, filter);
    }

    /**
     * Function to reset to original list.
     * @method: reset
     */
    vm.reset = function() {
      vm.pageNo = 1;
      getCourses(vm.pageNo);
    }

    /**
     * Function to get the list courses for user and this is will called based on sort, filter and even on the intial page load.
     * @method: getCourses
     */
    function getCourses(page, sort, filter) {
      var payload = {};
      payload.pageNo = page;

      //checking if sort is applied
      if (sort) {
        payload.sort = sort;
      }

      //checking if filter is applied
      if (filter) {
        payload.filter = filter;
      }

      RestService.get('courses', payload)
        .then(function(response) {
          if (response.data) {
            if (response.data.isMore) {
              vm.showMoreAvaiable = true;
            }
            vm.count = response.data.count;
            vm.courses = response.data.courseList;
          }
        }, function(err) {
          $log.error(err);
        });

    }
  }
}());

/**
 * Single course controller
 */

(function() {
  'use strict';

  angular.module('app.course.controller', [])
    .controller('CourseController', CourseController);

  /** @ngInject */
  function CourseController($scope, $state, $stateParams, $location, $log, RestService) {
    var vm = this;

    // Data
    var courseId = $stateParams.courseId;

    // method
    /**
     * Service call to the course details by ID.
     */
    RestService.get('course/' + courseId)
      .then(function(response) {
        if (response.data) {
          vm.course = response.data.course;
          vm.ratings = [];
          var i;
          for (i = 0; i < vm.course.ratings; i++) {
            vm.ratings.push('icon-smile voted');
          }

          for (i = 0; i < (5 - vm.course.ratings); i++) {
            vm.ratings.push('icon-smile');
          }
          vm.includedItems = (vm.course.includes) ? vm.course.includes.split(',') : [];
          vm.schedules = vm.course.schedule.split('|');
          vm.features = [];
          vm.isParking = (vm.course.parking.length > 0) ? vm.features.push({
            'iconClass': 'icon_set_1_icon-27',
            'name': 'Parking'
          }) : false;
          vm.isAudio = (vm.course.languages.length > 0) ? vm.features.push({
            'iconClass': 'icon_set_1_icon-13',
            'name': 'Accessibiliy'
          }) : false;
          vm.features.push({
            'iconClass': 'icon_set_1_icon-83',
            name: vm.course.duration
          });
        }
      }, function(err) {
        $log.error(err);
      });

    vm.adultCount = 0;
    vm.childrenCount = 0;

    $scope.$watchGroup(['adultCount', 'childrenCount'], function(newValues) {
      vm.count = parseInt(newValues[0]) + parseInt(newValues[1]) || 0;
    });

    vm.increment = function(type) {
      if (type === 'adult') {
        if (vm.adultCount < 10) {
          vm.adultCount++;
        }
      } else {
        if (vm.childrenCount < 10) {
          vm.childrenCount++;
        }
      }
    }

    vm.decrement = function(type) {
      if (type === 'adult') {
        if (vm.adultCount > 0) {
          vm.adultCount--;
        }
      } else {
        if (vm.childrenCount > 0) {
          vm.childrenCount--;
        }
      }
    }

    vm.minDate = new Date().toISOString().split('T')[0];

    /**
     * Function to submit the form for compvare the booking
     */
    vm.errorSubmit = false;
    vm.submitBooking = function() {
      if (vm.adultCount == 0 && vm.childrenCount == 0) {
        vm.errorSubmit = true;
      } else {
        var payload = {
          emailAddress: vm.emailAddress,
          firstName: vm.firstName,
          lastName: vm.lastName,
          telephone: vm.telephone,
          courseId: vm.course.courseID,
          adultCount: vm.adultCount,
          childrenCount: vm.childrenCount,
          date: vm.selectedDate
        };

        RestService.post('addCourseBookingFromUser', payload)
          .then(function(response) {
            $location.path('course-confirmation/' + response.data.booking);
          }, function(err) {
            $log.error(err);
          });
      }
    }
  }
}());

/**
 * Course confirmation controller for acknowleding the booked course.
 */

(function() {
    'use strict';

    angular.module('app.course-confirmation.controller', [])
        .controller('CourseConfirmationController', CourseConfirmationController);

    /** @ngInject */
    function CourseConfirmationController(RestService, $location, $state, $stateParams, $log) {
      var vm = this;

      //Data
      var bookingID = $stateParams.reference;

      /**
      * Rest service call to retrieve the booking reference details for confirmation page.
      */
      RestService.get('booking/' + bookingID)
        .then(function(response) {
          if(response.data.status == 200 && response.data.success) {
            if(response.data.reference) {
              vm.booking = response.data.booking.booking;
              vm.reference = response.data.booking.reference;
              vm.bookingReference = response.data.bookingReference;
            } else {
              vm.booking = response.data.booking.booking;
            }
          }
        }, function(err) {
          $log.error(err);
        });
    }
}());

/**
* Contact controller, where the user submits as issue.
*/

(function() {
  'use strict';

  angular.module('app.contact.controller', [])
    .controller('ContactController', ContactController);

    /** @ngInject */
    function ContactController($log, RestService) {

      var vm = this;

      vm.notification = false;

      vm.submitQuery = function() {
        if(vm.verfication === 4) {
          RestService.post('/queryMessage', {firstName: vm.firstName, lastName: vm.lastName, emailAddress: vm.emailAddress, phone: vm.phone, message: vm.message})
            .then(function(response) {
              if(response.data.status === 200 && response.data.success) {
                vm.alertType = 'alert-success';
                vm.notification = true;
                vm.notificationMessage = 'Thank you, your requested has been submitted, our team will get back to you at the earliest.';
                vm.firstName = vm.lastName = vm.emailAddress = vm.phone = vm.message = vm.verfication = '';
              }
            }, function(err) {
              $log.error(err);
              vm.alertType = 'alert-danger';
              vm.notification = true;
              vm.message = 'Something went wrong, please contact the admin.';
            });
        }
      }

    }
}());

/**
 * Authenticate service
 */

(function() {
    'use strict';

    angular.module('app.authenticate', [])
        .service('AuthService', AuthService)
        .service('AuthInterceptor', AuthInterceptor);

    /** @ngInject */
    function AuthService($window) {
        var user = this;

        /**
         * post login function to store the token, user details in localStorage
         * @method logIn
         */
        user.logIn = function(token, role, userDetails) {
            $window.localStorage.setItem('isAuthenticated', true);
            $window.localStorage.setItem('token', token);
            $window.localStorage.setItem('role', role);
            $window.localStorage.setItem('userDetails', userDetails);
            $window.localStorage.setItem('fullname', JSON.parse(userDetails).first_name + ' ' + JSON.parse(userDetails).last_name);
        }


        /**
         * Function to clear the localStorage items for the app
         * @method: logOut
         */
        user.logOut = function() {
            $window.localStorage.removeItem('isAuthenticated');
            $window.localStorage.removeItem('token');
            $window.localStorage.removeItem('role');
            $window.localStorage.removeItem('userDetails');
            $window.localStorage.removeItem('fullname');
        }

        /**
         * Function to check if the user is authenticated checking the localStorage isAuthenticated items
         * @method: isAuthenticated
         */
        user.isAuthenticated = function() {
            return $window.localStorage.getItem('isAuthenticated') || false;
        }

        /**
         * Function to get the stored web token from localStorage
         * @method: getToken
         */
        user.getToken = function() {
            return $window.localStorage.getItem('token') || '';
        }

        /**
         * Function to get the user role from localStorage items
         * @method: getRole
         */
        user.getRole = function() {
            return $window.localStorage.getItem('role');
        }

        /**
         * Function to get user details from localStorage items
         * @method: getUserDetails
         */
        user.getUserDetails = function() {
            return $window.localStorage.getItem('userDetails');
        }

        /**
        * Function to get full name of the user
        * @method: getFullName
        */
        user.getFullName = function() {
          return ($window.localStorage.getItem('fullname')) ? $window.localStorage.getItem('fullname') : '';
        }

        return user;
    }

    /** @ngInject */
    function AuthInterceptor(AuthService) {
        var interceptorFactory = {};

        /**
         * Function for intercepting the request and appending the token to request header
         * @method: request
         */
        interceptorFactory.request = function(config) {
            var token = AuthService.getToken();

            if (token) {
                config.headers['authorization'] = 'Bearer ' + token;
            }
            return config;
        }

        return interceptorFactory;
    }
}());

/**
 * Activity controller to show the activity details.
 */

(function() {
  'use strict';

  angular.module('app.activity.controller', [])
    .controller('ActivityController', ActivityController);

  /** @ngInject */
  function ActivityController(RestService, $state, $stateParams, $location, $log, $scope) {
    var vm = this;

    // Data
    var activityId = $stateParams.activityId;
    vm.adultCount = 0;
    vm.childrenCount = 0;
    vm.errorSubmit = false;
    vm.isBookingAvailable = false;
    //method

    /**
     * Function to get the activity details
     */
    RestService.get('activity/' + activityId)
      .then(function(response) {
        if (response.data) {
          var activity = response.data.activity;
          vm.activity = activity;
          vm.includedItems = (activity.includes) ? activity.includes.split(',') : [];
          vm.features = [];
          vm.isParking = (activity.parking.length > 0) ? vm.features.push({
            'iconClass': 'icon_set_1_icon-27',
            'name': 'Parking'
          }) : false;
          vm.isAudio = (activity.languages.length > 0) ? vm.features.push({
            'iconClass': 'icon_set_1_icon-13',
            'name': 'Accessibiliy'
          }) : false;
          vm.features.push({
            'iconClass': 'icon_set_1_icon-83',
            name: activity.duration
          });
          vm.isBookingAvailable = (activity.status.toUpperCase() === 'A') ? true : false;
        }
      }, function(err) {
        $log.error(err);
      });



    /**
     * Watch for adult and children count.
     */
    $scope.$watchGroup([function() {
      return vm.adultCount;
    }, function() {
      return vm.childrenCount;
    }], function(newValue, oldValue) {
      if (vm.adultCount || vm.childrenCount) {
        vm.isBookingAvailable = true;
      }
      vm.count = parseInt(newValue[0]) + parseInt(newValue[1]) || 0;
    });

    /**
     * Function to increment the count.
     * @method: increment
     */
    vm.increment = function(type) {
      if (type === 'adult') {
        if (vm.adultCount < 10) {
          vm.adultCount++;
        }
      } else {
        if (vm.childrenCount < 10) {
          vm.childrenCount++;
        }
      }
    }

    /**
     * Function to decrement the count.
     * @method: decrement
     */
    vm.decrement = function(type) {
      if (type === 'adult') {
        if (vm.adultCount > 0) {
          vm.adultCount--;
        }
      } else {
        if (vm.childrenCount > 0) {
          vm.childrenCount--;
        }
      }
    }

    /**
     * Function to book for activity.
     * @method: bookActivity
     */
    vm.bookActivity = function() {
      if (vm.adultCount == 0 && vm.childrenCount == 0) {
        vm.errorSubmit = true;
      } else {
        var payload = {
          emailAddress: vm.emailAddress,
          firstName: vm.firstName,
          lastName: vm.lastName,
          telephone: vm.telephone,
          activityID: vm.activity._id.$oid,
          adultCount: vm.adultCount,
          childrenCount: vm.childrenCount
        };

        RestService.post('/addBookingActivityFromUser', payload)
          .then(function(response) {
            if (response.data.status === 200 || response.data.success) {
              $location.path('confirmation/' + response.data.booking);
            }
          }, function(err) {
            $log.error(err);
          });
      }
    }
  }
}());

